import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Filters from './finder__filters';
import createFocusTrap from 'focus-trap';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

function finder__mobilefilters(props) {
    const [display, setDisplay] = useState(false);
    const [focusTrap, setFocusTrap] = useState({});

    let filters = null;
    const getFilters = () => filters;

    useEffect(() => {
        if (!focusTrap.activate) {
            setFocusTrap(
                createFocusTrap(getFilters(), {
                    onDeactivate: () => setDisplay(false),
                    clickOutsideDeactivates: true,
                })
            );
        }

        if (display) {
            focusTrap.activate && focusTrap.activate();
            disableBodyScroll(getFilters());
        } else {
            focusTrap.deactivate && focusTrap.deactivate();
            enableBodyScroll(getFilters());
        }
    }, [display]);

    return (
        <div className="finder__mobilefilters" data-open={display}>
            <button
                type="button"
                className="finder__mobilefilters__toggle"
                aria-haspopup={true}
                aria-expanded={display}
                onClick={() => setDisplay(!display)}
            >
                <span>
                    <span className="fal fa-filter icon" aria-hidden="true" />{' '}
                    <span>
                        Filter {props.config.summariseAs.plural}
                        {Object.keys(props.query.facets).length > 0 &&
                            ` (${
                                Object.keys(props.query.facets).length
                            } applied)`}
                    </span>
                </span>
            </button>
            <div
                className="finder__mobilefilters__filters"
                ref={mobilefilters => {
                    filters = mobilefilters;
                }}
            >
                <div className="finder__mobilefilters__filters__content">
                    <Filters
                        config={props.config}
                        query={props.query}
                        response={props.response}
                        update={props.update}
                    />
                    <button
                        type="button"
                        className="finder__mobilefilters__apply"
                        aria-expanded={display}
                        onClick={() => setDisplay(!display)}
                        disabled={props.updating}
                    >
                        <span>
                            {props.updating ? (
                                <React.Fragment>
                                    <span
                                        className="fas fa-spinner fa-pulse icon"
                                        aria-hidden="true"
                                    />{' '}
                                    <span>Updating results…</span>
                                </React.Fragment>
                            ) : (
                                <React.Fragment>
                                    <span>
                                        {props.response &&
                                        props.response.summary &&
                                        props.response.summary.totalMatching ===
                                            1
                                            ? `View 1 ${props.config.summariseAs.singular}`
                                            : `View ${props.response.summary.totalMatching} ${props.config.summariseAs.plural}`}
                                    </span>
                                </React.Fragment>
                            )}
                        </span>
                    </button>
                </div>
            </div>
        </div>
    );
}

finder__mobilefilters.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    response: PropTypes.object,
    update: PropTypes.object,
    updating: PropTypes.bool,
};

export default finder__mobilefilters;
